/**
 * Constants declaration
 * @class
 * @ignore
 */
class ErrorCode {

    init () {

        // Download Error
        this.DOWNLOAD_ERROR                        = 4;
        this.DOWNLOAD_MPD_ERROR                    = 4001; // MPD 
        this.DOWNLOAD_XINK_ERROR                   = 4002; // XINK 
        this.DOWNLOAD_INIT_SEGMENT_ERROR           = 4003; // INIT SEGMENT 
        this.DOWNLOAD_MEDIA_SEGMENT_ERROR          = 4004; // MEDIA SEGMENT 
        this.DOWNLOAD_INDEX_SEGMENT_ERROR          = 4005; // INDEX SEGMENT 
        this.DOWNLOAD_SWITCHING_SEGMENT_ERROR      = 4006; // SWITCHING SEGMENT 
        this.DOWNLOAD_OTHER_ERROR                  = 4007; // OTHER 

        this.DOWNLOAD_FRAGMENT_ERROR               = 4101; // fragment
        this.DOWNLOAD_FRAGMENT_ERROR_RETRY_SUCCESS = 4102; // fragment
        this.DOWNLOAD_FRAGMENT_ERROR_RETRY_FAILURE = 4103; // fragment
        this.DOWNLOAD_FRAGMENT_ERROR_RANGE         = 4104; // fragmentRange (> 30M)
        this.DOWNLOAD_FRAGMENT_ERROR_TIMEOUT       = 4105; // fragment
        this.CONNECT_FRAGMENT_ERROR_TIMEOUT        = 4106; // fragment
        this.CONNECT_NC_FRAGMENT_ERROR_TIMEOUT     = 4107; //  nc fragment 

        // Manifest Error
        this.MANIFEST_ERROR                        = 5;    // MPD 
        this.MANIFEST_TYPE_ERROR                   = 5001; // MPD 
        this.MANIFEST_RESOLVE_ERROR                = 5002; // MPD 

        // Media Error
        this.MEDIA_ERROR                           = 6;    // 
        this.MEDIA_ABORTED_ERROR                   = 6001; // 
        this.MEDIA_NETWORK_ERROR                   = 6002; // 
        this.MEDIA_DECODE_ERROR                    = 6003; // 
        this.MEDIA_SUPPORTED_ERROR                 = 6004; // 
        this.MEDIA_ENCRYPTED_ERROR                 = 6005; // 
        this.MEDIA_UNKNOWN_ERROR                   = 6000; // 
    }

    constructor () {
        this.init();
    }
}

let errorCode = new ErrorCode();
export default errorCode;
